        if (item.menus) {
          if (data.perms) {
            var obj = {
              menuId: data[i].menuId,
              perms: data[i].perms
            };

            _this.$store.dispatch('SET_BUTTONS', obj);
          }
        }
      });
    },
    btn: function btn(e) {},
    // 保存链接(组件)的激活状态
    savaNavState: function savaNavState(activePath) {
